<template>
  <div class="home">
    <MglMap
      class="map"
      :accessToken="accessToken"
      :mapStyle="mapStyle"
      :center="center"
      :zoom.sync="zoom"
      @load="onMapLoaded"
    >
      <MglNavigationControl position="top-right"/>
    </MglMap>
    <v-card
      v-if="panelStation"
      class="overlay"
      width="374"
      rounded
      elevation="3"
    >
      <v-card-title>
        <span>
          {{ station.name }}
        </span>
        <v-spacer></v-spacer>
        <v-btn @click="panelStation = false" icon>
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-card-title>
      <v-card-subtitle>
        Октябрьская ж. д., {{station.region}} регион
        <v-divider/>
      </v-card-subtitle>

      <v-list
      >
        <v-list-item
          v-for="(way, i) in station.ways.public"
          :key="i"
          two-line
        >
          <v-list-item-content>
            <v-list-item-title>Путь {{ way.name }}</v-list-item-title>
            <v-list-item-subtitle> Условная длина {{ way.length }} </v-list-item-subtitle>
          </v-list-item-content>

          <v-list-item-action>
            <v-checkbox
              v-model="way.state"
              color="red"
            ></v-checkbox>
          </v-list-item-action>
        </v-list-item>
      </v-list>
    </v-card>
    <v-sheet
      v-show="zoom >=2 && zoom <= 4"
      class="overlay legend pa-2"
      rounded
    >
      <div>
        <span class="legend-number">26</span>
        <span class="font-weight-light">
          общий процент загрузки
        </span>
      </div>
      <div class="mt-1">Пути общего пользования</div>
      <div class="font-weight-light">
        <span class="mr-3"><span class="legend-key red"/> занято</span>
        <span class="legend-key light_red"/> свободно
      </div>
      <div class="mt-1">Пути необщего пользования</div>
      <div class="font-weight-light">
        <span class="mr-3"><span class="legend-key grey"/> занято</span>
        <span class="legend-key light_grey"/> свободно
      </div>
    </v-sheet>
  </div>
</template>

<script>
// @ is an alias to /src
import Mapbox from 'mapbox-gl'
import { MglMap, MglNavigationControl } from 'vue-mapbox'
import octoberRailway from '@/assets/october.json'
import mapStyle from '@/assets/map-style.json'

for (const station of octoberRailway.stations) {
  for (const way of station.ways.public) {
    way.state = Math.round(Math.random() * 0.8) === 1
  }
}

// code for creating an SVG donut chart from feature properties
function createDonutChart (props) {
  const offsets = []
  const counts = [
    props.mag1,
    props.mag2,
    props.mag3,
    props.mag4
  ]
  let total = 0
  for (let i = 0; i < counts.length; i++) {
    offsets.push(total)
    total += counts[i]
  }
  const fontSize =
    total >= 1000 ? 22 : total >= 100 ? 20 : total >= 10 ? 18 : 16
  const r = total >= 1000 ? 50 : total >= 100 ? 32 : total >= 10 ? 24 : 18
  const r0 = Math.round(r * 0.6)
  const w = r * 2

  let html =
    '<div><svg width="' +
    w +
    '" height="' +
    w +
    '" viewbox="0 0 ' +
    w +
    ' ' +
    w +
    '" text-anchor="middle" style="font: ' +
    fontSize +
    'px sans-serif; display: block">'

  const colors = ['#d6d6d6', '#969696', '#fdaeae', '#ff0000']
  for (let i = 0; i < counts.length; i++) {
    html += donutSegment(
      offsets[i] / total,
      (offsets[i] + counts[i]) / total,
      r,
      r0,
      colors[i]
    )
  }
  html +=
    '<circle cx="' +
    r +
    '" cy="' +
    r +
    '" r="' +
    r0 +
    '" fill="white" /><text dominant-baseline="central" transform="translate(' +
    r +
    ', ' +
    r +
    ')">' +
    total.toLocaleString() +
    '</text></svg></div>'

  const el = document.createElement('div')
  el.innerHTML = html
  return el.firstChild
}

function donutSegment (start, end, r, r0, color) {
  if (end - start === 1) end -= 0.00001
  const a0 = 2 * Math.PI * (start - 0.25)
  const a1 = 2 * Math.PI * (end - 0.25)
  const x0 = Math.cos(a0)
  const y0 = Math.sin(a0)
  const x1 = Math.cos(a1)
  const y1 = Math.sin(a1)
  const largeArc = end - start > 0.5 ? 1 : 0

  return [
    '<path d="M',
    r + r0 * x0,
    r + r0 * y0,
    'L',
    r + r * x0,
    r + r * y0,
    'A',
    r,
    r,
    0,
    largeArc,
    1,
    r + r * x1,
    r + r * y1,
    'L',
    r + r0 * x1,
    r + r0 * y1,
    'A',
    r0,
    r0,
    0,
    largeArc,
    0,
    r + r0 * x0,
    r + r0 * y0,
    '" fill="' + color + '" />'
  ].join(' ')
}

export default {
  name: 'Home',
  components: {
    MglMap,
    MglNavigationControl
  },

  data: () => ({
    accessToken: 'pk.eyJ1IjoiY2hpZ2EiLCJhIjoiY2tqYnRyN2NzN3B4ZTJxcWpvdmVldGdmYSJ9.mTIH5Hby4yDyU-LLFOY0Fw',
    mapStyle: mapStyle,
    zoom: 3,
    center: [83.214, 60.703],
    showed: true,
    test: 50,
    popups: {},
    octoberRailway: octoberRailway,
    panelStation: false,
    station: {
      name: null,
      lngLat: [],
      esr: null,
      ways: {
        public: [{
          name: null,
          length: null
        }],
        private: [{
          name: null,
          length: null
        }]
      },
      region: null
    }
  }),

  watch: {
    octoberRailway: {
      handler: function () {
        const stationsFeatures = this.octoberRailway.stations.map(station => {
          const total = station.ways.public.length
          const busy = station.ways.public.reduce((acc, way) => {
            return acc + way.state
          }, 0)
          const load = busy / total
          station.load = load
          return {
            type: 'Feature',
            id: station.esr,
            properties: {
              load,
              name: station.name,
              esr: station.esr
            },
            geometry: {
              type: 'Point',
              coordinates: station.lngLat
            }
          }
        })

        const stationsGeoJSON = {
          type: 'FeatureCollection',
          features: stationsFeatures
        }
        this.$store.map.getSource('stations').setData(stationsGeoJSON)
      },
      deep: true
    }
  },

  created () {
    this.mapbox = Mapbox
  },

  methods: {
    onMapLoaded: function (event) {
      this.$store.map = event.map

      // set russian language for all labels
      // this.$store.map.setLayoutProperty('country-label', 'text-field', ['get', 'name_ru'])
      // this.$store.map.setLayoutProperty('state-label', 'text-field', ['get', 'name_ru'])
      // this.$store.map.setLayoutProperty('settlement-label', 'text-field', ['get', 'name_ru'])
      // this.$store.map.setLayoutProperty('settlement-subdivision-label', 'text-field', ['get', 'name_ru'])
      // this.$store.map.setLayoutProperty('airport-label', 'text-field', ['get', 'name_ru'])
      // this.$store.map.setLayoutProperty('transit-label', 'text-field', ['get', 'name_ru'])
      // this.$store.map.setLayoutProperty('poi-label', 'text-field', ['get', 'name_ru'])
      // this.$store.map.setLayoutProperty('water-point-label', 'text-field', ['get', 'name_ru'])
      // this.$store.map.setLayoutProperty('water-line-label', 'text-field', ['get', 'name_ru'])
      // this.$store.map.setLayoutProperty('natural-point-label', 'text-field', ['get', 'name_ru'])
      // this.$store.map.setLayoutProperty('natural-line-label', 'text-field', ['get', 'name_ru'])
      // this.$store.map.setLayoutProperty('waterway-label', 'text-field', ['get', 'name_ru'])
      // this.$store.map.setLayoutProperty('road-label', 'text-field', ['get', 'name_ru'])

      // add railway areas
      this.$store.map.addSource('rail-areas', {
        type: 'geojson',
        data: require('@/assets/railAreas.json')
      })
      this.$store.map.addLayer({
        id: 'rail-polygons',
        type: 'fill',
        source: 'rail-areas',
        layout: {},
        maxzoom: 6,
        paint: {
          'fill-color': [
            'match',
            ['get', 'code'],
            [10, 24, 61, 83, 96],
            '#f58631',
            [28, 41, 63, 88],
            '#44b85c',
            [17, 51, 80, 92],
            '#0078bf',
            [1, 58, 76, 94],
            '#ffcb31',
            /* other */ 'grey'
          ],
          'fill-opacity': [
            'interpolate',
            ['linear'],
            ['zoom'],
            0,
            0.7,
            2,
            0.20,
            5,
            0.20,
            6,
            0
          ]
        }
      })
      this.$store.map.addLayer({
        id: 'rail-borders',
        type: 'line',
        source: 'rail-areas',
        layout: {
          'line-join': 'round',
          'line-cap': 'round'
        },
        minzoom: 5.5,
        paint: {
          'line-color': 'purple',
          'line-width': 2,
          'line-opacity': [
            'interpolate',
            ['linear'],
            ['zoom'],
            5.5,
            0,
            6.5,
            0.4
          ]
        }
      })

      // add stations
      const stationsFeatures = this.octoberRailway.stations.map(station => {
        const total = station.ways.public.length
        const busy = station.ways.public.reduce((acc, way) => {
          return acc + way.state
        }, 0)
        const load = busy / total
        station.load = load
        return {
          type: 'Feature',
          id: station.esr,
          properties: {
            load,
            name: station.name,
            esr: station.esr
          },
          geometry: {
            type: 'Point',
            coordinates: station.lngLat
          }
        }
      })

      const stationsGeoJSON = {
        type: 'FeatureCollection',
        features: stationsFeatures
      }

      this.$store.map.addSource('stations', {
        type: 'geojson',
        data: stationsGeoJSON
      })
      // setInterval(() => {
      //   const stationsFeatures = octoberRailway.stations.map(station => {
      //     const load = Math.random()
      //     return {
      //       type: 'Feature',
      //       properties: {
      //         load,
      //         name: station.name
      //       },
      //       geometry: {
      //         type: 'Point',
      //         coordinates: [station.latLng[1], station.latLng[0]]
      //       }
      //     }
      //   })
      //
      //   const stationsGeoJSON = {
      //     type: 'FeatureCollection',
      //     features: stationsFeatures
      //   }
      //   this.$store.map.getSource('stations').setData(stationsGeoJSON)
      // }, 500)
      this.$store.map.addLayer({
        id: 'station-point',
        type: 'circle',
        source: 'stations',
        minzoom: 3.8,
        paint: {
          'circle-stroke-width': 1,
          'circle-stroke-color': 'white',
          'circle-radius': {
            stops: [
              [3, 2.5],
              [6, 5],
              [12, 7]
            ]
          },
          'circle-color': [
            'interpolate',
            ['linear'],
            ['get', 'load'],
            0,
            'green',
            0.5,
            'yellow',
            1,
            'red'
          ],
          'circle-opacity': [
            'interpolate',
            ['linear'],
            ['zoom'],
            3.8,
            0,
            4,
            1
          ]
        }
      })

      // objects for caching and keeping track of HTML marker objects (for performance)
      const markers = {}
      let markersOnScreen = {}
      const map = this.$store.map
      function updateMarkers () {
        const newMarkers = {}
        const features = map.querySourceFeatures('rails')
        // for every cluster on the screen, create an HTML marker for it (if we didn't yet),
        // and add it to the map if it's not there already
        for (let i = 0; i < features.length; i++) {
          const coords = features[i].geometry.coordinates
          const props = features[i].properties
          const id = props.id

          let marker = markers[id]
          if (!marker) {
            const el = createDonutChart(props)
            marker = markers[id] = new Mapbox.Marker({
              element: el
            }).setLngLat(coords)
          }
          newMarkers[id] = marker

          if (!markersOnScreen[id]) marker.addTo(map)
        }
        // for every marker we've added previously, remove those that are no longer visible
        for (const id in markersOnScreen) {
          if (!newMarkers[id]) markersOnScreen[id].remove()
        }
        markersOnScreen = newMarkers
      }

      // after the GeoJSON data is loaded, update markers on the screen and do so on every map move/moveend
      this.$store.map.on('data', (e) => {
        if (e.sourceId !== 'rails') return
        this.$store.map.on('move', updateMarkers)
        this.$store.map.on('moveend', updateMarkers)
        updateMarkers()
      })

      this.$store.map.addSource('rails', {
        type: 'geojson',
        data: {
          type: 'FeatureCollection',
          features: [
            {
              type: 'Feature',
              properties: {
                mag1: 5,
                mag2: 10,
                mag3: 15,
                mag4: 20,
                mag5: 25
              },
              geometry: {
                type: 'Point',
                coordinates: [34.361234, 62.771296]
              }
            }
          ]
        }
      })

      map.addLayer({
        id: 'earthquake_circle',
        type: 'circle',
        source: 'rails',
        minzoom: 2,
        maxzoom: 4
      })

      map.on('click', 'station-point', (event) => {
        this.openPanelStation(event.features[0].properties.esr)
      })
      map.on('mouseenter', 'station-point', function () {
        map.getCanvas().style.cursor = 'pointer'
      })
      map.on('mouseleave', 'station-point', function () {
        map.getCanvas().style.cursor = ''
      })
      this.$store.map.on('moveend', () => {
        if (this.zoom > 8) {
          const features = map.queryRenderedFeatures({ layers: ['station-point'] })
          // let updatedPopups = []
          for (const feature of features) {
            const id = feature.id
            const coordinates = feature.geometry.coordinates
            if (!Object.prototype.hasOwnProperty.call(this.popups, id)) {
              this.popups[id] = new Mapbox.Popup({
                closeButton: false,
                closeOnClick: false
              })
                .setLngLat(coordinates)
                .setHTML(
                  feature.properties.name
                )
                .addTo(map)
            } else {
              this.popups[id].setLngLat(coordinates)
            }
          }
        } else {
          for (const id in this.popups) {
            const popup = this.popups[id]
            popup.remove()
            delete this.popups[id]
          }
        }
      })
    },
    openPanelStation (esr) {
      const index = this.octoberRailway.stations.findIndex(station => {
        return station.esr === esr
      })
      this.station = this.octoberRailway.stations[index]
      this.panelStation = true
    }
  }
}
</script>

<style scoped lang="sass">
.map
  position: absolute
  top: 0
  bottom: 0
  width: 100%

.overlay
  position: absolute
  user-select: none
  margin: 10px

.legend
  box-shadow: 0 0 0 2px rgba(0,0,0,.1) !important
  bottom: 0
  right: 0
  font-size: 0.8rem
  .legend-key
    display: inline-block
    border-radius: 20%
    width: 10px
    height: 10px
    margin-right: 5px
  .red
    background-color: #FF0000
  .light_red
    background-color: #FDAEAE
  .grey
    background-color: #969696
  .light_grey
    background-color: #D6D6D6
  .legend-number
    padding: 2px
    display: inline-block
    border: 1px black solid
    border-radius: 50%
    font: 16px sans-serif
    margin-right: 5px
</style>
